/**
 * alertTips 警告提示
 * @description 警告提示，展现需要关注的信息
 * @tutorial https://uviewui.com/components/alertTips.html
 * @property {String} title 辅助性文字，颜色比title浅一点，字号也小一点，可选
 * @property {String} description 显示的标题文字
 * @property {String} type 关闭按钮(默认为叉号icon图标)
 * @property {String} close-able 用文字替代关闭图标，close-able为true时有效
 * @property {Boolean} show-icon 是否显示左边的辅助图标
 * @property {Boolean} show 显示或隐藏组件
 * @event {Function} click 点击组件时触发
 * @event {Function} close 点击关闭按钮时触发
 */
export default {
	name: 'u-alert-tips',
	props: {
		// 显示文字
		title: {
			type: String,
			default: ''
		},
		// 主题，success/warning/info/error
		type: {
			type: String,
			default: 'warning'
		},
		// 辅助性文字
		description: {
			type: String,
			default: ''
		},
		// 是否可关闭
		closeAble: {
			type: Boolean,
			default: false
		},
		// 关闭按钮自定义文本
		closeText: {
			type: String,
			default: ''
		},
		// 是否显示图标
		showIcon: {
			type: Boolean,
			default: false
		},
		// 文字颜色，如果定义了color值，icon会失效
		color: {
			type: String,
			default: ''
		},
		// 背景颜色
		bgColor: {
			type: String,
			default: ''
		},
		// 边框颜色
		borderColor: {
			type: String,
			default: ''
		},
		// 是否显示
		show: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
		}
	},
	watch: {
		show(val) {
			if (val == false) {
				// 先opacity隐藏，再移除
				setTimeout(() => {
					this.showAlert = false;
				}, 300);
			}
		}
	},
	computed: {
		// 计算字体颜色，如果没有自定义的，就用uview主题颜色
		computeColor() {
			if (this.color) return this.color;
			else return this.$u.color[this.type];
		},
		// 计算背景颜色
		computeBgColor() {
			if (this.bgColor) return this.bgColor;
			return this.$u.color[this.type + 'Light'];
		},
		computeBorderColor() {
			if (this.borderColor) return this.borderColor;
			return this.$u.color[this.type + 'Disabled'];
		}
	},
	methods: {
		// 点击内容
		click() {
			this.$emit('click');
		},
		// 点击关闭按钮
		close() {
			this.$emit('close');
		}
	}
}
